body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

.topnav {
    overflow: hidden;
    background-color: #333;
}

    .topnav a {
        float: left;
        color: #f2f2f2;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 17px;
    }

       a:hover {
            background-color: #aaa;
            color: black;
        }

        a.active {
            background-color: #04AA6D;
            color: white;
        }


.blankButton {
    color: #f2f2f2;
    background: none;
    border: none;
    height: 100%;
    width: 100%;
}